'use client';

import { useWizard } from '@/hooks/use-wizard';
import { WizardProgressBar } from './ProgressBar';
import { Button } from '@/components/ui/button';
import { ArrowLeft, ArrowRight, Check, Loader2 } from 'lucide-react';

export default function SetupWizard() {
  const {
    steps,
    activeStep,
    isFirstStep,
    isLastStep,
    goToNextStep,
    goToPreviousStep,
    isProcessing,
  } = useWizard();

  const ActiveStepComponent = steps[activeStep]?.component;

  return (
    <div className="flex h-full w-full flex-col">
      <div className="p-8 border-b">
        <WizardProgressBar />
      </div>

      <div className="flex-1 p-8 overflow-y-auto">
        {ActiveStepComponent && <ActiveStepComponent />}
      </div>

      <div className="p-4 border-t bg-background/80 backdrop-blur-sm mt-auto">
        <div className="flex justify-between items-center max-w-4xl mx-auto">
          <div>
            {!isFirstStep && (
              <Button variant="outline" onClick={goToPreviousStep} disabled={isProcessing}>
                <ArrowLeft className="mr-2 h-4 w-4" /> Previous
              </Button>
            )}
          </div>
          <div>
            <Button onClick={goToNextStep} disabled={isProcessing}>
                {isProcessing ? (
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                ) : isLastStep ? (
                    <Check className="mr-2 h-4 w-4" />
                ) : (
                    <ArrowRight className="mr-2 h-4 w-4" />
                )}
                {isProcessing ? 'Processing...' : isLastStep ? 'Finish Setup' : 'Next'}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
