'use client';

import type { ExtractedScheduleData } from '@/ai/schemas/extract-schedule-data-schema';
import { Card, CardHeader, CardTitle, CardDescription, CardContent } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Users, BookCopy, Briefcase } from 'lucide-react';

interface WizardStepReviewProps {
  extractedData: ExtractedScheduleData | null;
}

export default function WizardStepReview({ extractedData }: WizardStepReviewProps) {
  if (!extractedData) {
    return (
      <div className="max-w-2xl mx-auto text-center">
        <h2 className="text-2xl font-bold font-headline tracking-tight">Review Your Data</h2>
        <p className="mt-2 text-muted-foreground">
          You chose to skip the upload. We'll proceed with a blank slate. Click "Next" to continue.
        </p>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-8">
        <h2 className="text-2xl font-bold font-headline tracking-tight">Review Your Data</h2>
        <p className="mt-2 text-muted-foreground">
          Our AI has extracted the following information from your document. Please review it before we import it.
        </p>
      </div>
      <div className="grid gap-6 md:grid-cols-2">
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2"><Users className="h-5 w-5 text-primary" /> Volunteers</CardTitle>
            <CardDescription>{extractedData.volunteers.length} unique volunteers found.</CardDescription>
          </CardHeader>
          <CardContent>
            <ScrollArea className="h-48">
              <ul className="space-y-2">
                {extractedData.volunteers.map((v, i) => (
                  <li key={i} className="text-sm p-2 bg-muted/50 rounded-md">{v.name}</li>
                ))}
              </ul>
            </ScrollArea>
          </CardContent>
        </Card>
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2"><Briefcase className="h-5 w-5 text-primary" /> Roles</CardTitle>
            <CardDescription>{extractedData.roles.length} unique roles found.</CardDescription>
          </CardHeader>
          <CardContent>
            <ScrollArea className="h-48">
              <ul className="space-y-2">
                {extractedData.roles.map((r, i) => (
                  <li key={i} className="text-sm p-2 bg-muted/50 rounded-md">{r.name}</li>
                ))}
              </ul>
            </ScrollArea>
          </CardContent>
        </Card>
        <Card className="md:col-span-2">
          <CardHeader>
            <CardTitle className="flex items-center gap-2"><BookCopy className="h-5 w-5 text-primary" /> Suggested Templates</CardTitle>
            <CardDescription>{extractedData.templates.length} service templates suggested.</CardDescription>
          </CardHeader>
          <CardContent>
            <ScrollArea className="h-48">
              <div className="space-y-4">
                {extractedData.templates.map((t, i) => (
                  <div key={i} className="text-sm p-3 border rounded-md">
                    <p className="font-semibold">{t.name}</p>
                    <p className="text-xs text-muted-foreground">Roles: {t.roles.join(', ')}</p>
                  </div>
                ))}
              </div>
            </ScrollArea>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
