// Using a custom hook to manage state and simulate backend
export function useMockDatabase() {
  const [events, setEvents] = useState<Event[]>(initialEvents);
  const [volunteers, setVolunteers] = useState<Volunteer[]>(initialVolunteers);
  const [universalRoles, setUniversalRoles] = useState<UniversalRole[]>(initialUniversalRoles);
  const [serviceTemplates, setServiceTemplates] = useState<ServiceTemplate[]>(initialServiceTemplates);
  const [availability, setAvailability] = useState<VolunteerAvailability[]>(initialAvailability);
  const [church, setChurch] = useState<Church>(initialChurch);
  const [families, setFamilies] = useState<Family[]>(initialFamilies);


  const getEvents = useCallback(async (churchId: string): Promise<Event[]> => {
    return events.filter(event => event.churchId === churchId);
  }, [events]);

  const createEvent = useCallback(async (newEventData: Omit<Event, 'id' | 'roles' | 'churchId'>, templateId?: string): Promise<Event> => {
    let newRoles: Role[] = [];
    if (templateId) {
        const template = serviceTemplates.find(t => t.id === templateId);
        if (template) {
            newRoles = template.roles.map(templateRole => ({
                id: `role_${Date.now()}_${templateRole.instanceId}`,
                roleName: templateRole.name,
                assignedVolunteerId: null,
                assignedVolunteerName: null,
                status: 'Pending',
            }));
        }
    }

    const newEvent: Event = {
      ...newEventData,
      id: `event_${Date.now()}`,
      churchId: MOCK_CHURCH_ID,
      roles: newRoles,
    };
    setEvents(prev => [...prev, newEvent]);
    return newEvent;
  }, [serviceTemplates]);

  const updateEvent = useCallback(async (eventId: string, updatedData: Partial<Event>): Promise<void> => {
    setEvents(prev => prev.map(event => event.id === eventId ? { ...event, ...updatedData } : event));
  }, []);

  const addRoleToEvent = useCallback(async (eventId: string, roleData: Omit<Role, 'id'>): Promise<Role> => {
    const newRole: Role = {
      ...roleData,
      id: `role_${Date.now()}`,
    };
    setEvents(prev => prev.map(event => {
      if (event.id === eventId) {
        return { ...event, roles: [...event.roles, newRole] };
      }
      return event;
    }));
    return newRole;
  }, []);

  const updateRoleInEvent = useCallback(async (eventId: string, roleId: string, updatedRoleData: Partial<Role>): Promise<void> => {
    setEvents(prev => prev.map(event => {
      if (event.id === eventId) {
        return {
          ...event,
          roles: event.roles.map(role => role.id === roleId ? { ...role, ...updatedRoleData } : role),
        };
      }
      return event;
    }));
  }, []);

  const deleteRoleFromEvent = useCallback(async (eventId: string, roleId: string): Promise<void> => {
    setEvents(prev => prev.map(event => {
      if (event.id === eventId) {
        return { ...event, roles: event.roles.filter(role => role.id !== roleId) };
      }
      return event;
    }));
  }, []);
  
  const getMyAssignments = useCallback(async (volunteerId: string): Promise<Event[]> => {
    const publishedMonths = church.publishedMonths || [];
    return events
      .filter(event => {
        const eventMonth = `${event.eventDate.getFullYear()}-${String(event.eventDate.getMonth() + 1).padStart(2, '0')}`;
        return publishedMonths.includes(eventMonth);
      })
      .map(event => ({
        ...event,
        roles: event.roles.filter(role => role.assignedVolunteerId === volunteerId),
      }))
      .filter(event => event.roles.length > 0);
  }, [events, church.publishedMonths]);

  const updateAssignmentStatus = useCallback(async (eventId: string, roleId: string, status: RoleStatus): Promise<void> => {
    await updateRoleInEvent(eventId, roleId, { status });
  }, [updateRoleInEvent]);
  
  const getVolunteers = useCallback(async (): Promise<Volunteer[]> => {
    return volunteers;
  }, [volunteers]);

  const getVolunteersSync = useCallback((): Volunteer[] => {
    return volunteers;
  }, [volunteers]);

  const updateVolunteerPreferences = useCallback(async (volunteerId: string, preferences: VolunteerPreferences): Promise<void> => {
    setVolunteers(prev => prev.map(v => 
        v.id === volunteerId
            ? { ...v, preferences }
            : v
    ));
  }, []);

  // Universal Roles CRUD
  const getUniversalRoles = useCallback(async (): Promise<UniversalRole[]> => {
    return universalRoles;
  }, [universalRoles]);
  
  const addUniversalRole = useCallback(async (name: string, description?: string): Promise<UniversalRole> => {
      const newRole: UniversalRole = { id: `urole_${Date.now()}`, name, description };
      setUniversalRoles(prev => [...prev, newRole]);
      return newRole;
  }, []);

  const updateUniversalRole = useCallback(async (id: string, updates: Partial<Omit<UniversalRole, 'id'>>): Promise<void> => {
    setUniversalRoles(prev => prev.map(r => r.id === id ? {...r, ...updates} : r));
    // Also update role name in templates if it changed
    if (updates.name) {
      setServiceTemplates(prev => prev.map(t => ({
        ...t,
        roles: t.roles.map(tr => tr.roleId === id ? { ...tr, name: updates.name! } : tr)
      })))
    }
  }, []);

  const deleteUniversalRole = useCallback(async (id: string): Promise<void> => {
      setUniversalRoles(prev => prev.filter(r => r.id !== id));
      // Also remove from any templates
      setServiceTemplates(prev => prev.map(t => ({...t, roles: t.roles.filter(role => role.roleId !== id) })));
  }, []);

  // Service Templates CRUD
  const getServiceTemplates = useCallback(async (): Promise<ServiceTemplate[]> => {
    return serviceTemplates;
  }, [serviceTemplates]);

  const createServiceTemplate = useCallback(async (name: string, roles: TemplateRole[]): Promise<ServiceTemplate> => {
      const newTemplate: ServiceTemplate = { id: `template_${Date.now()}`, name, roles };
      setServiceTemplates(prev => [...prev, newTemplate]);
      return newTemplate;
  }, []);

  const updateServiceTemplate = useCallback(async (id: string, updates: Partial<Omit<ServiceTemplate, 'id'>>): Promise<void> => {
      setServiceTemplates(prev => prev.map(t => t.id === id ? {...t, ...updates} : t));
  }, []);
  
  const deleteServiceTemplate = useCallback(async (id: string): Promise<void> => {
      setServiceTemplates(prev => prev.filter(t => t.id !== id));
  }, []);

  // Volunteer Availability
  const getVolunteerAvailability = useCallback(async (volunteerId: string): Promise<Date[]> => {
      const family = families.find(f => f.members.some(m => m.id === volunteerId)) || families.find(f => f.id === MOCK_FAMILY_ID);
      if (!family) return [];

      if (volunteerId === 'all') {
          if (family.members.length === 0) return [];
          
          const memberAvailabilities: Date[][] = family.members.map(member => {
              return availability.find(a => a.volunteerId === member.id)?.unavailableDates || [];
          });

          if (memberAvailabilities.length === 0) return [];

          // Find the intersection of all unavailability arrays. A day is only a shared unavailable day if EVERY member is unavailable.
          const sharedUnavailableDates = memberAvailabilities.reduce((acc, current) => {
              const currentTimestamps = new Set(current.map(d => d.getTime()));
              return acc.filter(date => currentTimestamps.has(date.getTime()));
          });

          return sharedUnavailableDates;

      } else {
        const record = availability.find(a => a.volunteerId === volunteerId);
        return record ? record.unavailableDates : [];
      }
  }, [availability, families]);

 const toggleVolunteerAvailability = useCallback(async (volunteerId: string, date: Date): Promise<void> => {
    setAvailability(prevAvailability => {
      let newAvailability = [...prevAvailability];
      const family = families.find(f => f.members.some(m => m.id === volunteerId)) || families.find(f => f.id === MOCK_FAMILY_ID);
      const memberIds = family ? family.members.map(m => m.id) : [volunteerId];

      if (volunteerId === 'all') {
        const isUnavailableForAll = memberIds.every(id => 
          newAvailability.find(a => a.volunteerId === id)?.unavailableDates.some(d => isSameDay(d, date))
        );

        memberIds.forEach(id => {
          let record = newAvailability.find(a => a.volunteerId === id);
          if (record) {
            const dateExists = record.unavailableDates.some(d => isSameDay(d, date));
            let updatedDates;
            if (isUnavailableForAll) { // Make available
              if (dateExists) {
                updatedDates = record.unavailableDates.filter(d => !isSameDay(d, date));
              } else {
                updatedDates = record.unavailableDates;
              }
            } else { // Make unavailable
              if (!dateExists) {
                updatedDates = [...record.unavailableDates, date];
              } else {
                updatedDates = record.unavailableDates;
              }
            }
            newAvailability = newAvailability.map(a => a.volunteerId === id ? { ...a, unavailableDates: updatedDates } : a);
          } else if (!isUnavailableForAll) {
            newAvailability.push({ volunteerId: id, unavailableDates: [date] });
          }
        });

      } else {
        let record = newAvailability.find(a => a.volunteerId === volunteerId);
        if (record) {
          const dateExists = record.unavailableDates.some(d => isSameDay(d, date));
          const updatedDates = dateExists
            ? record.unavailableDates.filter(d => !isSameDay(d, date))
            : [...record.unavailableDates, date];
          newAvailability = newAvailability.map(a => a.volunteerId === volunteerId ? { ...a, unavailableDates: updatedDates } : a);
        } else {
          newAvailability.push({ volunteerId: volunteerId, unavailableDates: [date] });
        }
      }
      return newAvailability;
    });
  }, [families]);
  
  // Church Settings
  const getChurchSync = useCallback((): Church => {
    return church;
  }, [church]);

  const getChurch = useCallback(async (): Promise<Church> => {
    return church;
  }, [church]);

  const updateChurch = useCallback(async (updates: Partial<Omit<Church, 'id'>>): Promise<void> => {
    setChurch(prev => ({ ...prev, ...updates }));
  }, []);

  const isMonthPublished = useCallback((monthStr: string): boolean => {
    return church.publishedMonths?.includes(monthStr) ?? false;
  }, [church.publishedMonths]);

  const publishMonth = useCallback((monthStr: string) => {
    setChurch(prev => {
        const publishedMonths = prev.publishedMonths || [];
        if (publishedMonths.includes(monthStr)) {
            return prev; // Already published
        }
        return { ...prev, publishedMonths: [...publishedMonths, monthStr] };
    })
  }, []);


  // Family
  const getFamilyByVolunteerId = useCallback(async (volunteerId: string): Promise<Family | null> => {
      const volunteer = volunteers.find(v => v.id === volunteerId);
      if (!volunteer?.familyId) return null;
      return families.find(f => f.id === volunteer.familyId) ?? null;
  }, [volunteers, families]);

  const addFamilyMember = useCallback(async (familyId: string, name: string, email: string): Promise<void> => {
      const newVolunteer: Volunteer = {
          id: `user_volunteer_${Date.now()}`,
          name,
          email,
          phone: '',
          familyId,
          preferences: {
              preferredRoleIds: [],
              servingFrequency: 'as-needed',
              familyServingPreference: 'possible',
              familyManagementEnabled: true,
          }
      };
      setVolunteers(prev => [...prev, newVolunteer]);
      setFamilies(prev => prev.map(f => {
          if (f.id === familyId) {
              return { ...f, members: [...f.members, { id: newVolunteer.id, name, isPrimary: false }] };
          }
          return f;
      }));
  }, []);

  const removeFamilyMember = useCallback(async (familyId: string, volunteerId: string): Promise<void> => {
      setVolunteers(prev => prev.filter(v => v.id !== volunteerId));
      setFamilies(prev => prev.map(f => {
          if (f.id === familyId) {
              return { ...f, members: f.members.filter(m => m.id !== volunteerId) };
          }
          return f;
      }));
  }, []);


  return useMemo(() => ({ 
    events, 
    getEvents,
    createEvent,
    updateEvent,
    addRoleToEvent,
    updateRoleInEvent,
    deleteRoleFromEvent,
    getMyAssignments, 
    updateAssignmentStatus,
    getVolunteers,
    getVolunteersSync,
    updateVolunteerPreferences,
    universalRoles,
    getUniversalRoles,
    addUniversalRole,
    updateUniversalRole,
    deleteUniversalRole,
    serviceTemplates,
    getServiceTemplates,
    createServiceTemplate,
    updateServiceTemplate,
    deleteServiceTemplate,
    getVolunteerAvailability,
    toggleVolunteerAvailability,
    getChurchSync,
    getChurch,
    updateChurch,
    isMonthPublished,
    publishMonth,
    getFamilyByVolunteerId,
    addFamilyMember,
    removeFamilyMember,
    MOCK_CHURCH_ID,
    MOCK_VOLUNTEER_ID,
  }), [
    events, getEvents, createEvent, updateEvent, addRoleToEvent, updateRoleInEvent, deleteRoleFromEvent,
    getMyAssignments, updateAssignmentStatus, getVolunteers, getVolunteersSync, updateVolunteerPreferences,
    universalRoles, getUniversalRoles, addUniversalRole, updateUniversalRole, deleteUniversalRole, serviceTemplates,
    getServiceTemplates, createServiceTemplate, updateServiceTemplate, deleteServiceTemplate,
    getVolunteerAvailability, toggleVolunteerAvailability, getChurchSync, getChurch, updateChurch, isMonthPublished, publishMonth,
    getFamilyByVolunteerId, addFamilyMember, removeFamilyMember
  ]);
}

    
