
'use client';

import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import { useMockDatabase } from '@/lib/data';
import { WizardProvider } from '@/hooks/use-wizard';
import { extractScheduleData } from '@/ai/flows/extract-schedule-data-flow';
import { ExtractedScheduleDataSchema, type ExtractedScheduleData } from '@/ai/schemas/extract-schedule-data-schema';
import SetupWizard from '@/components/admin/setup-wizard/SetupWizard';
import WizardStepWelcome from '@/components/admin/setup-wizard/WizardStepWelcome';
import WizardStepUpload from '@/components/admin/setup-wizard/WizardStepUpload';
import WizardStepReview from '@/components/admin/setup-wizard/WizardStepReview';
import WizardStepFinish from '@/components/admin/setup-wizard/WizardStepFinish';
import { useToast } from '@/hooks/use-toast';

export default function SetupWizardPage() {
  const { isLoggedIn, isAdmin } = useAuth();
  const router = useRouter();
  const db = useMockDatabase();
  const { toast } = useToast();

  const [wizardData, setWizardData] = useState<ExtractedScheduleData | null>(null);

  useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (!isAdmin) {
      router.push('/my-schedule');
    }
  }, [isLoggedIn, isAdmin, router]);

  const handleFileUpload = async (file: File): Promise<ExtractedScheduleData | null> => {
    const reader = new FileReader();
    return new Promise((resolve, reject) => {
      reader.onerror = () => {
        reader.abort();
        reject(new DOMException("Problem parsing input file."));
      };

      reader.onload = async () => {
        const dataUri = reader.result as string;
        try {
          const result = await extractScheduleData({ documentDataUri: dataUri });
          const validatedData = ExtractedScheduleDataSchema.parse(result);
          setWizardData(validatedData);
          resolve(validatedData);
        } catch (err) {
          console.error(err);
          toast({
            variant: "destructive",
            title: "Data Extraction Failed",
            description: "The AI could not process your document. Please check the file and try again.",
          });
          reject(err);
        }
      };

      reader.readAsDataURL(file);
    });
  };
  
  const handleSkip = () => {
    setWizardData({
      volunteers: [],
      roles: [],
      events: [],
      templates: [],
    });
  };

  const handleFinish = async () => {
    if (!wizardData) return;

    try {
        // In a real app, you would have a single transactional write.
        // For the mock DB, we can do them sequentially.
        for (const role of wizardData.roles) {
            await db.addUniversalRole(role.name, role.description);
        }
        for (const template of wizardData.templates) {
             const templateRoles = template.roles.map(roleName => {
                // This is brittle, relies on mock data having the role already
                const uRole = db.universalRoles.find(ur => ur.name === roleName);
                return {
                    instanceId: `tr_${Date.now()}_${Math.random()}`,
                    roleId: uRole?.id || '',
                    name: roleName
                }
            })
            await db.createServiceTemplate(template.name, templateRoles);
        }
        // NOTE: We are not adding volunteers or events in this step to keep it simple.
        // A real implementation would add volunteers and create the scheduled events.

        localStorage.setItem('setupCompleted', 'true');
        toast({
            title: "Setup Complete!",
            description: "Your church is ready to go.",
        });
        router.push('/admin');

    } catch (error) {
        toast({
            variant: "destructive",
            title: "Save Failed",
            description: "There was an error saving your setup data. Please try again.",
        })
    }
  };

  if (!isLoggedIn || !isAdmin) {
    return null; // or a loading spinner
  }

  const steps = [
    { id: 'Step 1', name: 'Welcome', component: WizardStepWelcome },
    { 
      id: 'Step 2', 
      name: 'Upload Schedule', 
      component: () => <WizardStepUpload onFileUpload={handleFileUpload} onSkip={handleSkip} /> 
    },
    { 
      id: 'Step 3', 
      name: 'Review & Confirm', 
      component: () => <WizardStepReview extractedData={wizardData} />
    },
    { 
        id: 'Step 4', 
        name: 'Finish', 
        component: WizardStepFinish,
        onNext: handleFinish
    },
  ];

  return (
    <div className="h-screen w-screen bg-muted flex flex-col">
       <div className="p-4 border-b shrink-0">
           <h1 className="text-2xl font-bold font-headline">Weaver's Loom Setup</h1>
       </div>
       <div className="flex-1 overflow-hidden">
        <WizardProvider steps={steps}>
            <SetupWizard />
        </WizardProvider>
      </div>
    </div>
  );
}
