
'use client';
import { z } from 'zod';

export const ExtractedVolunteerSchema = z.object({
  name: z.string().describe('The full name of the volunteer.'),
  email: z.string().optional().describe('The email address, if found.'),
  phone: z.string().optional().describe('The phone number, if found.'),
});

export const ExtractedRoleSchema = z.object({
    name: z.string().describe('The name of the volunteer role.'),
    description: z.string().optional().describe('A generated one-sentence description of the role.'),
});

export const ExtractedEventSchema = z.object({
    eventName: z.string().describe('The name of the service or event.'),
    eventDate: z.string().datetime().describe('The date and time of the event in ISO 8601 format.'),
    roles: z.array(z.object({
        roleName: z.string().describe('The role for this event.'),
        assignedVolunteerName: z.string().optional().describe('The name of the volunteer assigned to this role for this event.'),
    })).describe('The list of roles for this specific event.'),
});

export const SuggestedTemplateSchema = z.object({
    name: z.string().describe("A suggested name for the template (e.g., 'Sunday Morning Service')."),
    roles: z.array(z.string()).describe('A list of role names that make up this template.'),
});

export const ExtractedScheduleDataSchema = z.object({
  volunteers: z.array(ExtractedVolunteerSchema).describe('A list of all unique volunteers found in the document.'),
  roles: z.array(ExtractedRoleSchema).describe('A list of all unique volunteer roles found in the document.'),
  events: z.array(ExtractedEventSchema).describe('A list of all specific events or services found.'),
  templates: z.array(SuggestedTemplateSchema).describe('A list of suggested service templates based on recurring event structures.'),
});

export type ExtractedScheduleData = z.infer<typeof ExtractedScheduleDataSchema>;

export const ExtractScheduleDataInputSchema = z.object({
  documentDataUri: z
    .string()
    .describe(
      "The volunteer schedule document (e.g., PDF, Word, Excel) as a data URI. Must include a MIME type and use Base64 encoding. Format: 'data:<mimetype>;base64,<encoded_data>'."
    ),
});

export type ExtractScheduleDataInput = z.infer<typeof ExtractScheduleDataInputSchema>;
