
'use server';
/**
 * @fileOverview Extracts structured data from a volunteer schedule document.
 * 
 * - extractScheduleData - A function that parses a document and extracts volunteer, role, and event data.
 * - ExtractScheduleDataInput - The input type for the function.
 * - ExtractedScheduleData - The return type for the function.
 */

import { ai } from '@/ai/genkit';
import type { ExtractScheduleDataInput, ExtractedScheduleData } from '@/ai/schemas/extract-schedule-data-schema';
import { ExtractScheduleDataInputSchema, ExtractedScheduleDataSchema } from '@/ai/schemas/extract-schedule-data-schema';


export async function extractScheduleData(input: ExtractScheduleDataInput): Promise<ExtractedScheduleData> {
  const prompt = ai.definePrompt({
    name: 'extractScheduleDataPrompt',
    input: { schema: ExtractScheduleDataInputSchema },
    output: { schema: ExtractedScheduleDataSchema },
    prompt: `You are an expert data extraction assistant for a church volunteer scheduling app called Weaver's Loom. Your task is to analyze the provided document and extract all relevant scheduling information.

The document is a volunteer schedule. It could be a PDF, Word document, or an Excel spreadsheet.

Analyze the document provided via the data URI: {{media url=documentDataUri}}

Extract the following information:

1.  **Volunteers**: Identify every unique person mentioned as a volunteer. Extract their full name. If available, also extract their email and phone number.
2.  **Roles**: Identify all the unique volunteer roles mentioned (e.g., "Lector", "Usher", "Acolyte", "Coffee Hour Host"). For each role, provide a simple, friendly one-sentence description.
3.  **Events**: List every specific service or event instance. For each event, extract its name (e.g., "Sunday Morning Eucharist"), its full date and time (as an ISO 8601 string), and a list of roles required for that specific event, including who is assigned to each role.
4.  **Templates**: Analyze the events to find recurring patterns. If multiple events have the same name and a similar set of roles (e.g., every "Sunday Morning Eucharist" has a Lector, two Ushers, and an Acolyte), create a suggested service template. The template should have a name and a list of the role names it includes.

Return the extracted data in the structured JSON format defined by the output schema. Pay close attention to data types and formats, especially for dates.`,
  });

  const { output } = await prompt(input);
  return output!;
}
